/* Quote Modal Styles - Scoped to prevent affecting other pages */
.modalOverlay {
  position: fixed;
  inset: 0;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
}

.modalContent {
  background: #f5f5f5;
  border-radius: 4px;
  max-width: 700px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.modalHeader {
  position: sticky;
  top: 0;
  background: #f5f5f5;
  padding: 16px 20px;
  display: flex;
  justify-content: flex-end;
  z-index: 10;
  border-bottom: 1px solid #e0e0e0;
}

.closeButton {
  padding: 8px;
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.closeButton:hover {
  background: rgba(0, 0, 0, 0.05);
}

.closeIcon {
  width: 20px;
  height: 20px;
  color: #666;
}

.modalBody {
  padding: 0;
  background: #f5f5f5;
}

.modalFormWrapper {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

